::-webkit-scrollbar {
    width: 1vh;
    border: none;
  }
  
::-webkit-scrollbar-track {
background: black;
}

::-webkit-scrollbar-thumb {
background-color: white;
border-radius: 20px;
border: 0.4vh solid black;
}

.app {
    display: flex;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    background-color: black;
    font-family: 'Special Elite', cursive;
}

.app .content {
    width: 100vw;
    max-width: 1920px;
}