@import url('https://fonts.googleapis.com/css2?family=Special+Elite&display=swap');

* {
    padding: 0;
    margin: 0;
}

button {
    font-family: 'Special Elite', cursive;
}

@keyframes esvaecer {
    0% {
        background-color: rgba(0, 0, 0, 1);
    }
    25% { 
        background-color: rgba(0, 0, 0, 0.9);
    }
    50% { 
        background-color: rgba(0, 0, 0, 0.8);
    }
    75% { 
        background-color: rgba(0, 0, 0, 0.7);
    }
    100% { 
        background-color: rgba(0, 0, 0, 0.6);
    }
    
}

@keyframes rotacao360 {
    0% {
        transform: rotate(0);
    }
    25% {
        transform: rotate(180deg);
    }
    50% {
        transform: rotate(360deg);
    }
    50.1%{
        transform: rotate(0);
    }
    75% {
        transform: rotate(180deg);
    }
    90% {
        opacity: 100%;
    }
    100% {
        opacity: 0%;
        transform: rotate(360deg);
    }
    
}

.modalRolagem {
    position: absolute;
    font-family: 'Special Elite', cursive;
    z-index: 1000;
    display: none;
    height: 100vh;
    width: 100vw;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
}

.modalRolagem .dadoAnimacao {
    display: none;
    position: absolute;
    height: 20vh;
}

.modalRolagem .dadoAnimacao img {
    height: 100%;
}

.modalRolagem .contentModal {
    display: flex;
    flex-direction: column;
    background-color: black;
    border: solid white 0.4vh;
    width: 36%;
    height: auto;
    min-height: 36%;
}

.modalRolagem .contentModal .cabecalhoRolagem {
    display: flex;
    width: 100%;
    height: 20%;
    border-bottom: solid white 0.4vh;
    align-items: center;
    box-sizing: border-box;
    padding: 2vh 3vh;
    justify-content: space-between;
}

.modalRolagem .contentModal .cabecalhoRolagem .tituloRolagem {
    color: white;
    font-size: 2.4vh;
}

.modalRolagem .contentModal .cabecalhoRolagem .buttonFechar {
    background: none;
    cursor: pointer;
}

.modalRolagem .contentModal .cabecalhoRolagem .buttonFechar:hover {
    opacity: 50%;
}

.modalRolagem .contentModal .cabecalhoRolagem .buttonFechar img {
    height: 2vh;
}

.modalRolagem .contentModal .resultado {
    display: flex;
    width: 100%;
    box-sizing: border-box;
    flex-direction: column;
    padding: 2vh;
    color: white;
}

.modalRolagem .contentModal .resultado .resultadoFinal {
    display: flex;
    width: 100%;
    justify-content: center;
    font-size: 12vh;
    padding: 4vh 0vh;
}

.modalRolagem .contentModal .resultado .descricao {
    display: flex;
    width: 100%;
    justify-content: center;
    font-size: 8vh;
}

.modalRolagem .contentModal .resultado .descricao.sucesso {
    color: rgb(74, 255, 74);
}

.modalRolagem .contentModal .resultado .descricao.fracasso {
    color: rgb(255, 42, 42);
}

.modalRolagem .contentModal .resultado .rolagens {
    display: flex;
    flex-direction: column;
}

.modalRolagem .contentModal .resultado .rolagens .rolagem{
    margin-bottom: 1vh;
    font-size: 3vh;
}

.ordoRealitas {
