}

@keyframes save {
    0% {
        border-color: black;
        background-color: rgb(94, 255, 94);
    }
    25% { 
        border-color: black;
        background-color: rgb(44, 181, 44);
    }
    50% { 
        border-color: black;
        background-color: rgb(63, 136, 63);
    }
    75% { 
        border-color: black;
        background-color: rgb(55, 91, 55);
    }
    100% { 
        border-color: black;
        background-color: rgb(0, 0, 0);
    }
    
