@import url('https://fonts.googleapis.com/css2?family=Special+Elite&display=swap');

.playersView {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: white;
    height: 100vh;
    width: 100vw;
    box-sizing: border-box;
    padding: 3.13vh 3.7vw;
    font-family: 'Special Elite', cursive;
}

.playersView .visualizarPlayers {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
}

.playersView .visualizarPlayers .titulo {
    margin-bottom: 2vh;
    font-size: 6vh;
}

.selectPlayer {
    display: flex;
    width: 100%;
}

.player {
    display: flex;
    justify-content: space-between;
    border-radius: 1vw;
    padding: 2vh;
    width: 100%;
    background-color: rgb(70, 70, 70);
    margin-bottom: 2vh;
    box-sizing: border-box;
}

.player .info {
    display: flex;
    flex-direction: column;
}

.player .info .nomeJogador {
    font-size: 4vh;
}

.player .info .nomePersonagem {
    font-size: 3vh;
}

.selectPlayer .lixeira {
    display: flex;
    align-items: center;
    background: none;
    border: none;
    padding-bottom: 1vh;
    margin: 2vh;
    cursor: pointer;
}

.selectPlayer .lixeira:hover {
    opacity: 70%;
}

.selectPlayer .lixeira img {
    padding: 1vh;
    border-radius: 1vh;
    background-color: rgb(196, 12, 12);

}

.player:hover {
    color: black;
    background-color: rgb(177, 177, 177);
    cursor: pointer;
}

.playersView .adicionarPlayer {
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
    padding: 2vh;
    box-sizing: border-box;
}

.playersView .adicionarPlayer .button {
    padding: 1vh 2vh;
    font-size: 2vh;
    color: black;
    cursor: pointer;
    border-radius: 0.8vh;
    background: none;
    background-color: white;
    font-family: 'Special Elite', cursive;
}

.playersView .adicionarPlayer .button:hover {
    opacity: 70%;
}

.modal {
    position: none;
    font-family: 'Special Elite', cursive;
    z-index: 1000;
    height: 100vh;
    width: 100vw;
    left: 0;
    top: 0;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
}


.modal .contentModal {
    display: flex;
    flex-direction: column;
    background-color: black;
    border: solid white 0.4vh;
    width: 36%;
    height: auto;
    min-height: 36%;
}

.modal .contentModal .cabecalhoModal {
    display: flex;
    width: 100%;
    height: 20%;
    border-bottom: solid white 0.4vh;
    align-items: center;
    box-sizing: border-box;
    padding: 2vh 3vh;
    justify-content: space-between;
}

.modal .contentModal .cabecalhoModal .titulo {
    color: white;
    font-size: 2.4vh;
}

.modal .contentModal .cabecalhoModal .buttonFechar {
    background: none;
    cursor: pointer;
}

.modal .contentModal .cabecalhoModal .buttonFechar:hover {
    opacity: 50%;
}

.modal .contentModal .cabecalhoModal .buttonFechar img {
    height: 2vh;
}

.modal .buttons .button {
    background: none;
    color: white;
    padding: 1vh;
    border: 0.2vh solid white;
    font-size: 2vh;
    cursor: pointer;
    margin-left: 2vh;
    border-radius: 0.4vh;
}

.modal .buttons .button.adicionar {
    background: white;
    color: black;
    border: 0.2vh solid white;
}

.modal .buttons .button:hover {
    opacity: 70%;
}

.modalApagarPlayer {
    display: none;
}

.modalApagarPlayer .contentModal {
    min-height: auto;
}

.modalApagarPlayer .content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    min-height: 26vh;
    width: 100%;
    box-sizing: border-box;
    padding: 4vh;
}

.modalApagarPlayer .content .texto {
    color: white;
    font-weight: 200;
    font-size: 4vh;
    text-align: center;
}

.modalApagarPlayer .content .buttons {
    display: flex;
    box-sizing: border-box;
    width: 100%;
    margin-top: 2vh;
    display: flex;
    justify-content: right;
}
