.criarPersonagem {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    box-sizing: border-box;
    max-width: 1920px;
    padding: 4vh 2vh;
}

.criarPersonagem .titulo {
    display: flex;
    justify-content: center;
    width: 100%;
    margin-bottom: 2vh;
}

.criarPersonagem .titulo .texto {
    font-size: 4vh;
    color: white;
}

.modeloGeral {
    display: none;
    height: 100%;
    width: 100%;
    flex-direction: row;
    border: 0.4vh solid white;
    border-radius: 2vh;
    color: white;
    box-sizing: border-box;
    padding: 2vh;
    gap: 2vh;
}

.infoImportantes.active {
    display: flex;
}

.inventario.active {
    display: flex;
}

.pericias.active {
    display: flex;
}

.ataques.active {
    display: flex;
}

.habilidades.active {
    display: flex;
}

.outros.active {
    display: flex;
}

.criarPersonagem .column1,
.criarPersonagem .column2 {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.criarPersonagem .input {
    display: flex;
    flex-direction: column;
    justify-content: left;
    box-sizing: border-box;
    padding-bottom: 2vh;
}

.criarPersonagem .input .titulo {
    display: flex;
    justify-content: left;
    margin: 0.2vh;
}

.criarPersonagem .input .conteudo {
    background: none;
    border: none;
    color: white;
    font-size: 2vh;
    font-family: 'Special Elite', cursive;
    padding-bottom: 0.1vh;
    border-style: none;
    border-bottom: solid 0.2vh grey;
}

.criarPersonagem .input .conteudo .option {
    background-color: black;
    color: white;
}

.buttonOption {
    display: flex;
    box-sizing: border-box;
    width: 100%;
    padding: 2vh;
    display: flex;
    justify-content: right;
}

.buttonOption .button {
    background: none;
    color: white;
    padding: 1vh;
    border: 0.2vh solid white;
    font-size: 2vh;
    cursor: pointer;
    margin-left: 2vh;
    border-radius: 0.4vh;
}

.buttonOption .button.full {
    background: white;
    color: black;
    border: 0.2vh solid white;
}

.buttonOption .button:hover {
    opacity: 70%;
}